<template>
  <v-card>
    <v-card-title>
      <v-btn @click="addUser" color="primary">新增用户</v-btn>
      <v-spacer/>
      <v-text-field
        append-icon="search"
        label="搜索"
        single-line
        hide-details
        v-model="search"
      />
    </v-card-title>
    <v-divider/>
    <v-data-table
      :headers="headers"
      :items="items"
      :total-items="totalItems"
      :loading="loading"
      class="elevation-1"
      item-key="uid"
      hide-actions
    >
      <template slot="items" slot-scope="props">
        <tr @click="props.expanded = !props.expanded">
        <td class="text-xs-center">{{ props.item.uid }}</td>
        <td class="text-xs-center">{{ props.item.name }}</td>
        <td class="text-xs-center">{{props.item.email}}</td>
        <td class="text-xs-center">{{ props.item.role }}</td>
        <td class="justify-center layout px-0">
          <v-btn icon @click.stop="editUser(props.item)">
            <i class="el-icon-edit"/>
          </v-btn>
          <v-btn icon @click.stop="deleteUser(props.item)">
            <i class="el-icon-delete"/>
          </v-btn>
        </td>
        </tr>
      </template>
      <template slot="expand" slot-scope="props">
        <v-card flat>
          <v-card-text class="text-xs-center">{{props.item.password}}</v-card-text>
        </v-card>
      </template>
      <template slot="no-data">
        <v-alert
          :value="true"
          color="info"
          icon="info"
          outline
        >
          数据正在加载中
        </v-alert>
      </template>
     <!-- <template slot="pageText" slot-scope="props">
        共{{props.itemsLength}}条,当前:{{ props.pageStart }} - {{ props.itemsLength }}
      </template>-->
    </v-data-table>

    <v-dialog v-model="show" max-width="600" scrollable v-if="show">
      <v-card>
        <v-toolbar dark dense color="primary">
          <v-toolbar-title>{{isEdit ? '修改用户' : '新增用户'}}</v-toolbar-title>
          <v-spacer/>
          <v-btn icon @click="show = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text class="px-5 py-2">
          <!-- 表单 -->
          <user-form :oldUser="user" :isEdit="isEdit" @close="show = false" :reload="getDataFromApi"/>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-card>

</template>

<script>
  import UserForm from './UserForm'
  import {userData} from '../../mockDB'

  export default {
    name: "UserManage",
    components: {
      UserForm
    },
    data() {
      return {
        expand:false,
        search: '',// 过滤字段
        totalItems: 0,// 总条数
        items: [],// 表格数据
        loading: true,
        pagination: {},// 分页信息
        headers: [// 表头
          {text: 'uid', align: 'center', value: 'uid',sortable: false},
          {text: '用户名', align: 'center', sortable: false, value: 'name'},
          {text: '邮箱', align: 'center', sortable: false, value: 'email'},
          {text: '角色', align: 'center', value: 'role', sortable: false,},
          {text: '操作', align: 'center', value: 'uid', sortable: false}
        ],
        show: false,// 是否弹出窗口
        user: {}, // 用户信息
        isEdit: false // 判断是编辑还是新增
      }
    },
    watch: {
      /*pagination: {
        handler() {
          this.getDataFromApi();
        },
        deep: true
      },*/
      search: {
        handler() {
          this.getDataFromApi();
        }
      },
      show(val, oldVal) {
        // 表单关闭后重新加载数据
        if (oldVal) {
          this.getDataFromApi();
        }
      }
    },
    mounted() {
      this.getDataFromApi();
    },
    methods: {
      addUser() {
        this.user = {};
        this.isEdit = false;
        this.show = true;

      },
      editUser(item) {
        this.user = item;
        this.isEdit = true;
        this.show = true;
        // 查询商品分类信息，进行回显
        /*this.$http.get("/item/category/bid/" + item.id)
          .then(resp => {
            this.user.role = resp.data;
          })*/

      },
      deleteUser(item) {
        this.$message.confirm('此操作将永久删除该品牌, 是否继续?').then(() => {
          // 发起删除请求
          this.$http.get("/UserManage/delete?uid=" + item.uid)
            .then(() => {
              // 删除成功，重新加载数据
              this.$message.success("删除成功！");
              this.getDataFromApi();
            }).catch(()=>{
            this.$message.info("操作异常！");
          })
        }).catch(() => {
          this.$message.info("删除已取消！");
        });

      },
      getDataFromApi() {
        this.loading = true;

        this.$http.get("/UserManage/list")
          .then(resp => {
            this.items=resp.data;
            this.loading = false;
          }).catch(error =>{
          this.$message.info("获取数据失败！");
        })
      }
    }
  }
</script>

<style scoped>

</style>



